import PageItemsApi from 'api/catalog/PageItems/PageItems';
import Footer from 'components/Footer';
import Header from 'components/Header';
import { AboutKids } from 'components/pages/AboutKids';
import useAppDispatch from 'helpers/hooks/useAppDispatch';
import { useGetUserData } from 'helpers/hooks/useGetUserData';
import { baseSSR } from 'helpers/utils/baseSSR';
import { serverSideTranslations } from 'next-i18next/serverSideTranslations';
import Head from 'next/head';
import React from 'react';
import { changeIsMobile } from 'store/isMobile/IsMobileStore';

const AboutKidsPage = ({ data }) => {
	const dispatch = useAppDispatch();

	useGetUserData();

	dispatch(changeIsMobile(data.isMobile));
	return (
		<>
			<Head>
				<meta
					name="description"
					content="TOTTI I Базовая одежда, отражающая характер. Одежда и аксессуары для женщин, детей и мужчин. Актуальность, комфорт и качество."
				></meta>
			</Head>
			<Header />
			<AboutKids catalogList={data.catalogList} />
			<Footer withDarkBGC={true} />
		</>
	);
};

export const getServerSideProps = async ({ req, locale }) => {
	const { isMobile } = await baseSSR(req);
	const pageItemsListApi = new PageItemsApi();
	const catalogListParams = {
		url: 'about-kids',
	};
	const pageItemsList = await pageItemsListApi.getPageItams(catalogListParams);
	return {
		props: {
			data: {
				catalogList: pageItemsList.data.data,
				isMobile,
			},
			...(await serverSideTranslations(locale, ['common'])),
		},
	};
};

export default AboutKidsPage;
